// material
import { Container, Grid } from '@material-ui/core';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';

import { HomeWelcome, HomeTotalMintedNFT, HomeWorkProgress } from '../components/_dashboard/home';

// status could be: in_progress, completed, to_do
const DECENTRALIZATION_TASKS = [
  {
    id: '1',
    name: 'Icon IPFS Design',
    avatar: './static/mock-images/avatars/avatar_3.jpg',
    message: 'Design IPFS hostable interface',
    status: 'in_progress',
    category: 'High Priority'
  },
  {
    id: '2',
    name: 'Icon',
    avatar: './static/mock-images/avatars/avatar_1.jpg',
    message: 'Deploy Github Workflow',
    status: 'completed',
    category: 'High Priority'
  }
];

const SMARTCONTRACT_TASKS = [
  {
    id: '1',
    name: 'NFT Smart Contract',
    avatar: './static/mock-images/avatars/avatar_3.jpg',
    message: 'Generate NFT stored on Crust',
    status: 'completed',
    category: 'High Priority'
  },
  {
    id: '2',
    name: 'Swap Icon',
    avatar: './static/mock-images/avatars/avatar_1.jpg',
    message: 'Swap NFT',
    status: 'in_progress',
    category: 'High Priority'
  }
];
// ----------------------------------------------------------------------

export default function GeneralApp() {
  const { themeStretch } = useSettings();

  return (
    <Page title="General: App">
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <HomeWelcome displayName="NFT Lovers" />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeTotalMintedNFT />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeTotalMintedNFT />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeTotalMintedNFT />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeWorkProgress title="Decentralization SwitchSwap" tasks={DECENTRALIZATION_TASKS} />
          </Grid>

          <Grid item xs={12} md={4}>
            <HomeWorkProgress title="Smart Contract Dev" tasks={SMARTCONTRACT_TASKS} />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
}
